import React, { useEffect, useState } from "react";
import { withRouter, Link } from "react-router-dom";
import logo from "../../../assets/Logo.svg";
import usermanagementicon from "../../../assets/UserManagement.svg";
import usermanagementredicon from "../../../assets/UserManagementRed.svg";
import useroverviewicon from "../../../assets/UserOverview.svg";
import useroverviewredicon from "../../../assets/UserOverviewRed.svg";
import signuprequestsicon from "../../../assets/SignUpRequests.svg";
import signuprequestsredicon from "../../../assets/SignUpRequestsRed.svg";
import checklisticon from "../../../assets/Checklist.svg";
import checklistredicon from "../../../assets/ChecklistRed.svg";
import history from "../../../utils/history";

const MenuLink = (props) => {
  const [icon, setIcon] = useState(
    props.currentMenu === props.menu.to ? props.menu.hoverIcon : props.menu.icon
  );
  return (
    <li
      className={props.currentMenu === props.menu.to ? "active" : ""}
      onMouseOver={() => {
        setIcon(props.menu.hoverIcon);
      }}
      onMouseOut={() => {
        setIcon(props.currentMenu === props.menu.to ? props.menu.hoverIcon : props.menu.icon);
      }}
      onClick={() => {}}
      key={props.index}
    >
      <Link to={props.menu.to}>
        <img src={icon} alt="" />
        {props.menu.name}
        {props.menu.sub && (
          <ul className="back_office_sidebar_sub">
            {props.menu.sub.map((menu, index) => {
                return <MenuLink
                currentMenu={props.currentMenu}
                menu={menu}
                index={index}
                key={index}
                ></MenuLink>;
              })}
          </ul>
        )}
      </Link>
    </li>
  );
};
export const BackOfficeSidebar: React.FC = () => {
  const backOfficeMenu = [
    {
      name: "User Management",
      icon: usermanagementicon,
      hoverIcon: usermanagementredicon,
      to: "/backoffice/usermanagement",
      sub: [
        {
          name: "User Overview",
          icon: useroverviewicon,
          hoverIcon: useroverviewredicon,
          to: "/backoffice/useroverview"
        }
      ]
    },
    {
      name: "Sign Up Requests",
      icon: signuprequestsicon,
      hoverIcon: signuprequestsredicon,
      to: "/backoffice/signuprequests"
    },
    {
      name: "Checklist Management",
      icon: checklisticon,
      hoverIcon: checklistredicon,
      to: "/backoffice/checklistmanagement"
    }
  ];
  const [currentMenu, setCurrentMenu] = useState(history.location.pathname);

  useEffect(() => {
    setCurrentMenu(history.location.pathname);
  }, [history]);
  return (
    <>
      <div className="back_office_sidebar">
        <div className="row">
          <div className="col">
            <img src={logo} alt="" />
            <div className="back_office_title">BACK OFFICE</div>
          </div>
        </div>
        <div className="row">
          <div className="col">
            <ul className="back_office_sidebar_menu">
              {backOfficeMenu.map((menu, index) => {
                return <MenuLink
                currentMenu={currentMenu}
                menu={menu}
                index={index}
                key={index}
                ></MenuLink>;
              })}
            </ul>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(BackOfficeSidebar);
